@import "variables";

body{
    border-bottom: 0px;
    
    :hover{
        border-bottom: 0;
    }
}
@media all and (min-width: 851px) {
    header{
        position: relative;
        z-index: 100;
        background: $nav-background;

        div{
            .top_menu{
                background: #f23522;
                width: 72.6%;
                height: 46px;
                float: right;

                ul{
                    li{
                        display: inline-block;
                    }
                }
            }

            .logo{
                width: 27.4;
                height: 105px;
                display: inline-block;
                position: relative;
                float: left;

                .region-logo .site-branding {
                    //margin-top: 0.429em;
                }

                .site-branding__logo{
                    
                    margin: 9px 50px;
                    text-decoration: none;

                    

                    img{
                        height: 85px;
                        width: auto;
                        
                    }  
                }
            }

            .main_menu{
                width: 72.5%;
                left: 10.11%;
                display: inline-block;
                height: 60px;
                position: relative;
            }
        }
    }
}